import React from 'react';
import Layout from "../components/layout"
import Banner from "../components/banner"
import SEO from "../components/seo"
import { useTranslation } from 'react-i18next'
import BootstrapTable from 'react-bootstrap-table-next'
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import paginationFactory from 'react-bootstrap-table2-paginator'
import {Link} from 'gatsby';

const { SearchBar } = Search

function languageLinkFormatter(cell, row) {
  return (<Link to={"/languages/" + row.iso }>{ cell }</Link>)
}

// Basic country component
export default ({ pageContext: { country } }) => {
  const { t, i18n } = useTranslation()

  return (
  <Layout>
    <SEO title={ country.name } description={ country.overview } lang={ i18n.language } />
    <Banner
        title={ country.name }
        subtitle={ t('countries.of_region') + ' ' + t('countries.continents.' + country.continent.toLowerCase()) }
        image={`https://images.bible.cloud/fab/banners/country/${country.id}.jpg`}
        noGradient='true'
        iconImage={`https://images.bible.cloud/maps/icons/${country.id.toLowerCase()}_white.png`}
        breadcrumbs={[
          {path: '/', title: 'home'},
          {path: '/countries/', title: t('countries.title')},
          {path: '#', title: country.name }
        ]}
    />
    <div className="row">
    <aside className="medium-3 columns country">
      <h1 className="text-center mt-20">{ country.name }</h1>
      { country.joshua_project ?
          <ul className='fact-group row'>
            <div className='medium-6 columns'><b>Primary Religion:</b> { country.joshua_project.primary_religion }</div>
            <div className='medium-6 columns'><b>Language Name:</b> { country.joshua_project.language_official_name }</div>
            <div className='medium-6 columns'><b>Population:</b> { country.joshua_project.population }</div>
            <div className='medium-6 columns'><b>Official Language</b> { country.joshua_project.language_official_name}</div>
          </ul> : ''
      }

      <p>{ country.overview }</p>
    </aside>

    <section className="medium-9 columns">
      <div className="medium-10 columns">
        <ToolkitProvider
            keyField="id"
            data={ country.languages }
            bordered={ false }
            columns={[
              {
                dataField: 'id',
                text: 'ID',
                hidden: true,
                keyField: true
              },
              {
                dataField: 'name',
                text: 'Name',
                sort: true,
                formatter: languageLinkFormatter
              },
              {
                dataField: 'iso',
                text: 'iso',
                sort: true,
                classes: 'text-center'
              },
              {
                dataField: 'bibles_count',
                text: 'Bibles',
                classes: 'text-center'
              },
              {
                dataField: 'resources_count',
                text: 'Resources',
                classes: 'text-center'
              },
              {
                dataField: 'status_id',
                text: t('fields.status'),
                classes: 'text-center',
                formatter: cell => <span className={`lng_status_${cell}`}>{t('languages.status.' + cell)}</span>
              }
            ]}
            search
        >
          {
            props => (
              <div>
                <SearchBar {...props.searchProps} delay={100} placeholder={`Search ${country.languages.length} Languages`}/>
                <BootstrapTable pagination={ paginationFactory({
                  sizePerPageList: [
                    {text: '25', value: 25},
                    {text: '50', value: 50},
                    {text: '100', value: 100},
                    {text: 'all', value: country.languages.length}
                  ]
                }) } {...props.baseProps} />
              </div>
            )
          }
        </ToolkitProvider>
      </div>

    </section>
    </div>

  </Layout>)
}